.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.App-card {
  max-width: 360px;
  margin-top: 30px;
  margin: 20px;
  padding: 15px;
  min-height: 380px;
}

.App-cardcontent {
  max-width: 360px;
  margin-top: 30px;
  margin: 20px;
  padding: 15px;
  min-height: 100%;
}

.App-cardport {
  min-width: 380px;
  max-width: 380px;
  min-height: 380px;
  background-color: #aaa;
  align-items: center;
  justify-content: center;
  text-align: center;
}

html {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

body {
  margin: 0 auto;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  text-align: center;
}

img {
  width: 100%;
}

li {
  list-style: none;
}

h1, h2 {
  font-weight: 500;
}
